.CardContainer {
  display: flex;
  flex-direction: column;
  width: 375px;
  box-shadow: 10px 10px 20px #dddddd;
  border-radius: 20px;
}

@media only screen and (min-width: 730px) {
  .CardContainer {
    display: flex;
    flex-direction: row;
    width: 730px;
    height: 20rem;
  }
}
